@import "../../styles/mixins";

.main-content {
  flex-grow: 1;
  // .main-content__container
  &__container {
  }

  // .main-content__test-information
  &__test-information {
  }

  // .main-content__test-list
  &__test-list {
  }
}

.test-information {
  margin-top: 20px;
  height: 80px;
  // .test-information__head-wrapper
  &__head-wrapper {
    height: inherit;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
  }

  // .test-information__title
  &__title {
    & span {
      color: #5E5C74;
      font-size: 30px;
      font-family: 'Ubuntu', sans-serif;
    }
  }

  // .test-information__description
  &__description {
    & span {
      color: #8B9DAC;
      font-size: 20px;
    }
  }
}

.test-list {
  padding: 20px 15px 0; //1200-30=1170/3=390
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  @include adaptive-value('gap', 45, 40, 1);
  margin-bottom: 80px;
}