.test {
  width: 100%;
  padding: 20px 0 0;
}
.test__wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 100px 0 0;
}
.test__counter {
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.3s;
}
.test__counter span {
  color: white;
  font-size: 30px;
  font-family: "Ubuntu", sans-serif;
}
.test__close-icon {
  position: absolute;
  right: 0;
  top: 0;
}
.test__close-icon img {
  width: 30px;
  height: 30px;
}

.test-active__block {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}
.test-active__img {
  margin-bottom: 30px;
}
.test-active__img img {
  width: 200px;
  height: 200px;
}
.test-active__title {
  margin-bottom: 30px;
}
.test-active__title span {
  color: white;
  font-size: 50px;
}
.test-active__button {
  padding: 13px 60px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  transition: all 0.3s;
  border: 2px solid transparent;
}
.test-active__button:hover {
  border: 2px solid white;
  background: rgba(0, 0, 0, 0.1);
}
.test-active__button button {
  background: none;
}
.test-active__button button span {
  color: white;
  font-size: 20px;
}
.test-active__description {
  min-height: 26vh;
  align-self: flex-start;
}
.test-active__description span {
  font-size: 25px;
  color: white;
}

