.test-started {
  width: 100%;
  padding-left: 50px;
}
.test-started__title {
  margin-bottom: 80px;
}
.test-started__title span {
  font-size: 40px;
  font-family: "Ubuntu", sans-serif;
}
.test-started__answers li {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 30px;
}
.test-started__answers li span {
  font-size: 25px;
}

.selector {
  width: 30px;
  height: 30px;
  background-color: #f1f4f7;
  border: 2px solid #f1f4f7;
  border-radius: 50%;
  margin-right: 50px;
}

