.main-content {
  flex-grow: 1;
}
.test-information {
  margin-top: 20px;
  height: 80px;
}
.test-information__head-wrapper {
  height: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}
.test-information__title span {
  color: #5E5C74;
  font-size: 30px;
  font-family: "Ubuntu", sans-serif;
}
.test-information__description span {
  color: #8B9DAC;
  font-size: 20px;
}

.test-list {
  padding: 20px 15px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 45px;
  margin-bottom: 80px;
}
@media (max-width: 1230px) {
  .test-list {
    gap: calc(40px + 5 * ((100vw - 320px) / 910));
  }
}

