.card {
  width: 350px;
  height: 390px;
  transition: transform .3s;
  &:hover {
    transform: scale(1.02);
    .card__arrow {
      opacity: 1;
    }
  }
  // .card__wrapper
  &__wrapper {
    height: inherit;
    margin: 20px;
    position: relative;
    display: flex;
    align-items: center;
    border-radius: 20px;
  }

  // .card__information
  &__information {
    color: white;
    padding-left: 10px;
    height: 80px;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
  }

  // .card__category
  &__category {
    & span {
      font-size: 20px;
    }
  }

  // .card__title
  &__title {
    & span {
      font-size: 25px;
    }
  }

  // .card__arrow
  &__arrow {
    opacity: 0;
    transition: opacity .3s;
    & img {
      width: 30px;
      height: 30px;
    }
  }

  // .card__img
  &__img {
    position: absolute;
    right: -20px;
    bottom: -10px;
    & img {
      width: 200px;
      height: auto;
    }
  }
}