.test {
  width: 100%;
  padding: 20px 0 0;
  // .test__wrapper
  &__wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 100px 0 0;
  }

  // .test__counter
  &__counter {
    position: absolute;
    left: 0;
    top: 0;
    transition: all .3s;
    & span {
      color: white;
      font-size: 30px;
      font-family: 'Ubuntu', sans-serif;
    }
  }

  // .test__close-icon
  &__close-icon {
    position: absolute;
    right: 0;
    top: 0;

    & img {
      width: 30px;
      height: 30px;
    }
  }
}

.test-active {
  // .test-active__block
  &__block {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
  }

  // .test-active__img
  &__img {
    margin-bottom: 30px;
    & img {
      width: 200px;
      height: 200px;
    }
  }

  // .test-active__title
  &__title {
    margin-bottom: 30px;
    & span {
      color: white;
      font-size: 50px;
    }
  }

  // .test-active__button
  &__button {
    padding: 13px 60px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    transition: all .3s;
    border: 2px solid transparent;
    &:hover {
      border: 2px solid white;
      background: rgba(0, 0, 0, 0.1);
    }
    & button {
      background: none;
      & span {
        color: white;
        font-size: 20px;
      }
    }
  }

  // .test-active__description
  &__description {
    min-height: 26vh;
    align-self: flex-start;
    & span {
      font-size: 25px;
      color: white;
    }
  }
}