.header {
  height: 80px;
  box-shadow: 0 2px 2px rgba(118, 117, 144, 0.12);
  // .header__container
  &__container {
    max-width: 100%;
    height: inherit;
  }

  // .header__body
  &__body {
    height: inherit;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .header__menu
  &__menu {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  // .header__logo
  &__logo {
    & span {
      color: darkgray;
      font-size: 30px;
      font-weight: bold;
      font-family: 'Ubuntu', sans-serif;
      cursor: default;
    }
  }

  // .header__nav
  &__nav {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 50px;
  }

  // .header__search
  &__search {
    max-width: 320px;
    height: inherit;
    display: flex;
    align-items: center;
    margin-right: 20px;
    & input {
      height: inherit;
      background-color: #f1f4f7;
      padding-left: 20px;
      line-height: 20px;
      font-size: 18px;
    }
  }

  // .header__search-icon
  &__search-icon {
    display: flex;
    align-items: inherit;
    height: inherit;
    background-color: #f1f4f7;
    padding: 0 15px;
    cursor: pointer;
    & img {
      width: 30px;
      height: 30px;
    }
  }

  // .header__profile
  &__profile {
    cursor: pointer;
    & img {
      width: 50px;
      height: 50px;
    }
  }
}

.header-logo-menu-wrapper {
  height: inherit;
  display: flex;
  align-items: center;
}

.logo-menu-separator {
  width: 30px;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  & span {
    position: absolute;
    height: 100%;
    width: 1px;
    background-color: lightgrey;
  }
}

.menu-burger {
  width: 30px;
  height: 20px;
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  & span {
    display: block;
    position: absolute;
    height: 4px;
    width: 30px;
    background-color: dodgerblue;
    border-radius: 25px;
  }
  &:before, &:after {
    content: "";
    height: 4px;
    width: 30px;
    background-color: dodgerblue;
    position: absolute;
    border-radius: 25px;
  }
  &:before {
    top: 0;
  }
  &:after {
    bottom: 0;
  }
}