.card {
  width: 350px;
  height: 390px;
  transition: transform 0.3s;
}
.card:hover {
  transform: scale(1.02);
}
.card:hover .card__arrow {
  opacity: 1;
}
.card__wrapper {
  height: inherit;
  margin: 20px;
  position: relative;
  display: flex;
  align-items: center;
  border-radius: 20px;
}
.card__information {
  color: white;
  padding-left: 10px;
  height: 80px;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
}
.card__category span {
  font-size: 20px;
}
.card__title span {
  font-size: 25px;
}
.card__arrow {
  opacity: 0;
  transition: opacity 0.3s;
}
.card__arrow img {
  width: 30px;
  height: 30px;
}
.card__img {
  position: absolute;
  right: -20px;
  bottom: -10px;
}
.card__img img {
  width: 200px;
  height: auto;
}

