.test-started {
  width: 100%;
  padding-left: 50px;
  // .test-started__title
  &__title {
    margin-bottom: 80px;
    & span {
      font-size: 40px;
      font-family: "Ubuntu", sans-serif;
    }
  }

  // .test-started__answers
  &__answers {
    & li {
      display: flex;
      align-items: center;
      position: relative;
      margin-bottom: 30px;
      & span {
        font-size: 25px;
      }
    }
  }
}

.selector {
  width: 30px;
  height: 30px;
  background-color: #f1f4f7;
  border: 2px solid #f1f4f7;
  border-radius: 50%;
  margin-right: 50px;
}